import Api from '../../api/wo';
	export default {
		onLoad:function(option){
			Api.getWOInfo(option.orderId).then(res=>{
				console.log(res)
				this.person=res.data.person
				this.phone=res.data.phone
				this.br=res.data.br
				this.fault=res.data.fault
				this.machine=res.data.machine
				this.description=res.data.description
				this.wosts = res.data.wosts
				this.woid = res.data.woid
				const tmp = res.data.images.split(';')
					if (tmp[0] !== '') {
					  const urls = []
					  for (const index in tmp) {
						const url = tmp[index]
						urls.push(url)
					  }
					  this.images = urls;
				   }
			})
		},
		data() {
			return {
				images: [],
				person: '',
				phone: '',
				fault: '',
				machine: '',
				br: '',
				description: '',
				wosts: '',
				woid: ''
			};
		},
		methods: {
			onImageTouch(index){
				uni.previewImage({
					current:index,
					urls:this.images
				})
			},
			flowDetail(){
				uni.navigateTo({
					url: '/pages/operation_his/operation_his?orderId=' + this.woid
				})
			},
			point(orderId){
				
			}
		}
	}
